
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body{
    font-family: 'Nunito';
}
h2{
    font-size: 18px;
    padding: 10px 20px;
    color: #575ed8;
}
li{
    list-style: none;
}
.userlist, .privateChat{
    position: fixed;
    max-width: 200px;
    top: 30px;
}
.privateChat{
    display: none;
    right: 0;
    border: 1px solid #888;
    height: 200px;
    width: 200px;
}
.owlyme-chat{
    max-width: 600px;
    margin: 30px auto;
    border: 1px solid #ddd;
    -webkit-box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
            box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
    border-radius: 2px;
}
.chat-window{
    height: 400px;
    overflow: auto;
    background: #f9f9f9;
}
.output p{
    padding: 14px 0px;
    margin: 0 20px;
    border-bottom: 1px solid #e9e9e9;
    color: #555;
}
.output li{
    text-align: left;
}
.feedback p{
    color: #aaa;
    padding: 14px 0px;
    margin: 0 20px;
}
.output strong{
    color: #575ed8;
}
label{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
    padding: 10px 20px;
}
input{
    padding: 10px 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #eee;
    border: 0;
    display: block;
    width: 100%;
    background: #fff;
    border-bottom: 1px solid #eee;
    font-family: Nunito;
    font-size: 16px;
}
button{
    background: #575ed8;
    color: #fff;
    font-size: 18px;
    border: 0;
    padding: 12px 0;
    width: 100%;
    border-radius: 0 0 2px 2px;
}


